import table from '../data/table';
import {universities, russia_map} from '../data/russia';
import {speciality_plus, profession, payment} from '../data/selects';
import _ from "lodash";


const CalculateSummary = (profession: string, payment: string ): any => {
  let useful_sp = speciality_plus.filter(x => (x.payment === payment || payment === "3") && (x.profession === profession))
  let useful_sp_parted: any = {}
  useful_sp_parted.summary = useful_sp.filter(x => x.is_summary)
  useful_sp_parted.ranh = useful_sp.filter(x => !x.is_summary)

  // console.log(useful_sp_parted)


  console.log('useful_sp: ', useful_sp)
  let modern_table = table.map(x => {
    let region = universities.find(y => y.id === x.university)
    if (region) {
      return {
        ...x,
        region: region.region
      }
    }
  })
  let filt_table = modern_table.map((x: any) => {
    let newx = {
      region: x.region,
      university: x.university,
      year: x.year,
      ranh: 0,
      summary: 0,
    }


    useful_sp_parted.ranh.map((y: any) => {
      newx.ranh += x[y.id]
    })

    useful_sp_parted.summary.map((y: any) => {
      newx.summary += x[y.id]
    })


    return newx
  })
  // console.log(filt_table)

  let another: any = {}
  filt_table.forEach((elem: any) => {
    let key = elem.region + String(elem.year)
    if (!another[key]) {
      another[key] = elem
      delete another[key].university
    } else {
      another[key].ranh += elem.ranh
      another[key].summary += elem.summary
    }
    }
  )

  let another_v = _.groupBy(Object.values(another), 'region')

  let just_final = Object.keys(another_v).map((x: string) => {
    let newo: any = {region: x, data: {}}
    another_v[x].forEach((y: any) => {
      newo.data[y.year] = {ranh: y.ranh, summary: y.summary}
      newo.region_name = (russia_map.find((z: any) => z.key === x) || {}).value
    })
    return newo

  })

  // console.log(just_final)

  return just_final

}

const CalculateWholePaid = () => {
  let useful_sp = _.cloneDeep(speciality_plus)
  let useful_sp_parted: any = {}
  useful_sp.forEach(x => {
    let cat = x.is_summary ? 'summary' : 'ranh'
    let subcat = x.payment === '1' ? 'budget' : 'paid'
    if (!useful_sp_parted[cat]) {
      useful_sp_parted[cat] = {}
    }
    if (!useful_sp_parted[cat][subcat]) {
      useful_sp_parted[cat][subcat] = []
    }
    useful_sp_parted[cat][subcat].push(x)
  })
  // console.log(useful_sp_parted)


  let modern_table = _.cloneDeep(table).filter(x => x.year === 2021).map(x => {
    let region = _.cloneDeep(universities).find(y => y.id === x.university)
    if (region) {
      return {
        ...x,
        region: region.region
      }
    }
  })

  // console.log(modern_table)

  let filt_table = modern_table.map((x: any) => {
    let newx = {
      region: x.region,
      university: x.university,
      year: x.year,
      ranh: {
        budget: 0,
        paid: 0
      },
      summary: {
        budget: 0,
        paid: 0
      },
    }



    useful_sp_parted.ranh.budget.forEach((y: any) => {
      newx.ranh.budget += x[y.id]
    })

    useful_sp_parted.ranh.paid.forEach((y: any) => {
      newx.ranh.paid += x[y.id]
    })

    useful_sp_parted.summary.budget.forEach((y: any) => {
      newx.summary.budget += x[y.id]
    })

    useful_sp_parted.summary.paid.forEach((y: any) => {
      newx.summary.paid += x[y.id]
    })

    return newx
  })
  // console.log(filt_table)

  let another: any = {}

  filt_table.forEach((elem: any) => {
      let key = elem.region + String(elem.year)
      if (!another[key]) {
        another[key] = elem
        delete another[key].university
      } else {
        another[key].ranh.paid += elem.ranh.paid
        another[key].ranh.budget += elem.ranh.budget
        another[key].summary.paid += elem.summary.paid
        another[key].summary.budget += elem.summary.budget
      }
    }
  )

  let another_v = _.groupBy(Object.values(another), 'region')

  let just_final = Object.keys(another_v).map((x: string) => {
    let newo: any = {region: x, data: {}}
    another_v[x].forEach((y: any) => {
      newo.data = {ranh: y.ranh, summary: y.summary}
      newo.region_name = (russia_map.find((z: any) => z.key === x) || {}).value
    })
    return newo

  })

  console.log(just_final)

  return just_final
}

const getRegionName = (region: string) => {
  let reg = russia_map.find((x: any) => x.key === region) || {value: ''}
  return reg.value
}

export {
  CalculateSummary,
  getRegionName,
  CalculateWholePaid
}