import './MainScreen.scss'
import {connect} from "react-redux";
import logoRanepa from './../../sources/dias_logo.png'
import notification from './../../sources/svg/notification.svg'
import photo from './../../sources/svg/photoSample.svg'
import {Link, NavLink, Route, Routes} from 'react-router-dom'
import React from "react";
import Workspace from "../Workspace/Workspace";
import Simulator from "../Simulator/Simulator";
import FilialAnalysis from "../FilialAnalysis/FilialAnalysis";
import FilialAnalysis2 from "../FilialAnalysis2/FilialAnalysis2";


function MainScreen(props: Props) {
// ================================= STATE =================================

  const activeclassname = '--active'

// ================================= FUNCTIONS =================================

// ================================= LAYOUT =================================
  return (
    <div className="main-screen">

      <div className="side-panel">
        <div className="logo-wrapper">
          <Link to="/main" className="logo-ranepa">
            <img  src={logoRanepa} alt="logo" />
          </Link>

        </div>
      </div>

      <div className="screen-wrapper">
        <div className="top-panel">
          <div className="links">
            <NavLink to="analytics" className={({ isActive }) => isActive ? activeclassname : undefined}>Аналитика</NavLink>
            <NavLink to="simulator" className={({ isActive }) => isActive ? activeclassname : undefined}>Симулятор</NavLink>
            <NavLink to="dashboards" className={({ isActive }) => isActive ? activeclassname : undefined}>Дашборды</NavLink>
            <NavLink to="game" className={({ isActive }) => isActive ? activeclassname : undefined}>Игра</NavLink>
          </div>

          <div className="user-data">
            <NavLink to="notifications" className="notification">
              <img src={notification} alt="notif"/>
            </NavLink>
            <div className="user">
              <div className="name">Анна Смирнова</div>
              <div className="role">Роль пользователя</div>
            </div>
            <img className="user-photo" src={photo} alt="фото"/>

          </div>

        </div>


        <div className="work-wrapper">
          <Routes>
            <Route path="" element={<Workspace />} />
            <Route path="simulator" element={<Simulator/>} />
            <Route path="simulator/filial_analytics" element={<FilialAnalysis/>} />
            <Route path="simulator/filial_analytics2" element={<FilialAnalysis2/>} />
          </Routes>
        </div>
      </div>






    </div>
  );
}

// ================================= PROPS =================================

interface Props {

}

interface IMapStoreToProps {

}

interface IMapDispatchToProps {
  reduxActions: {},
}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {}
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {}
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(MainScreen);