import React from 'react';
import { BrowserRouter } from 'react-router-dom'
import { applyMiddleware, compose, createStore} from 'redux'
import ReactDOM from 'react-dom/client';
import { Provider } from 'react-redux'
import reduxThunk from 'redux-thunk'
import './index.css';
import App from './App';
import reportWebVitals from './reportWebVitals';
import rootReducer from "./redux/rootReducer";

import './variables.scss'

// Привязываем store к localStorage
////////////////////////////////////////////////////////////////
const storeLogger = (store: any) => (next: any) => (action: any) => {

  const result = next(action)
  if (action.type.indexOf('__DEV__') > -1) {
    console.log('DEV', store.getState())
  } else {
    console.log('Store:', store.getState())
  }
  return result
}

let persistedState = JSON.parse(localStorage.getItem('reduxState') || "{}")


const store = createStore(rootReducer, persistedState,
  compose(
    applyMiddleware(storeLogger, reduxThunk),
    // @ts-ignore
    window.__REDUX_DEVTOOLS_EXTENSION__ ? window.__REDUX_DEVTOOLS_EXTENSION__() : compose
  ))

store.subscribe(()=> {
  localStorage.setItem('reduxState', JSON.stringify(store.getState()))
})
////////////////////////////////////////////////////////////////


const root = ReactDOM.createRoot(
  document.getElementById('root') as HTMLElement
);
root.render(
  <React.StrictMode>
    <Provider store={store}>
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </Provider>
  </React.StrictMode>
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
