import './FilialAnalysis2.scss'
import {connect} from "react-redux";
import {useState} from "react";
import SelectsContainer from "../ui/SelectsContainer/SelectsContainer";
import {selects} from "../../data/selects";
import {CalculateSummary, CalculateWholePaid} from "../../mock/mockApi";
import {IAnalyzedCountries} from "../../redux/types";
import {replaceColors} from "../../redux/actions/actions";
import ProfessionAnalysisWithMap from "../ui/ProfessionAnalysisWithMap/ProfessionAnalysisWithMap";
import WholePaidAnalysisWithMap from "../ui/WholePaidAnalysisWithMap/WholePaidAnalysisWithMap";
import {IMapsType} from "../../types/filial";

function FilialAnalysis2(props: Props) {
// ================================= STATE =================================

  const [showmap, setShowmap] = useState<IMapsType>('select')
  const [selectsData, setSelects] = useState<any>({})
  const [analysis, setAnalysis] = useState<any>([])



// ================================= FUNCTIONS =================================

  const func = () => {
    setAnalysis(selectsData.sub_analysis.value === "whole" ? CalculateWholePaid() :CalculateSummary(selectsData.profession.value, selectsData.payment.value))
    setShowmap(selectsData.sub_analysis.value)
  }


// ================================= LAYOUT =================================
  return (
    <div className="filial-analysis">
      <div className="header">Анализ и оптимизация экономических показателей филиальной сети</div>


      <SelectsContainer className={showmap !== 'select' ? "--invisible" : ""} data={selects} selects={selectsData} setter={(key: string, value: any) => {setSelects({...selectsData, [key]: value})}}/>
      <button className={`lets-analyse ${showmap !== 'select' ? '--invisible' : ''}`} onClick={func}>Произвести анализ</button>

      {
        showmap === "profession" ?
          <ProfessionAnalysisWithMap setAnalysis={setAnalysis} showmap={showmap} analysis={analysis} selectsData={selectsData} setShowmap={setShowmap} chosenCountry={props.mapData.chosenCountry} />
          : showmap === "whole" ?
            <WholePaidAnalysisWithMap setAnalysis={setAnalysis} showmap={showmap} analysis={analysis} selectsData={selectsData} setShowmap={setShowmap} chosenCountry={props.mapData.chosenCountry} />
          :  null
      }

      {/*<ProfessionAnalysisWithMap className={showmap === "profession" ? "" : "--invisible"} setAnalysis={setAnalysis} showmap={showmap} analysis={analysis} selectsData={selectsData} setShowmap={setShowmap} chosenCountry={props.mapData.chosenCountry} />*/}

      {/*<WholePaidAnalysisWithMap className={showmap === "wholepaid" ? "" : "--invisible"} setAnalysis={setAnalysis} showmap={showmap} analysis={analysis} selectsData={selectsData} setShowmap={setShowmap} chosenCountry={props.mapData.chosenCountry} />*/}

    </div>
  );
}

// ================================= PROPS =================================

interface Props {
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void
  },
  mapData: {
    chosenCountry: string
  }
}

interface IMapStoreToProps {
  mapData: {
    chosenCountry: string
  }
}

interface IMapDispatchToProps {
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void
  },
}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {
    mapData: {
      chosenCountry: store.mapData.chosenCountry
    }
  }
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {
      replaceColors: (coloredCountries: IAnalyzedCountries) => dispatch(replaceColors(coloredCountries))
    },
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(FilialAnalysis2);