import './Country.scss';
import {IMapReducer} from "../redux/types";
import {setChosenCountry} from "../redux/actions/actions";
import {connect} from "react-redux";

function Country (props: Props) {
  return (
    <g className={`country-${props.data.key} ${props.color || ""}`} onClick={props.onClick}>
    {
          typeof props.data.path === "string" ?
            <path className="country" d={props.data.path} key={`${props.data.key}`}></path>
              :
            props.data.path.map((x: string, i: number) => {
              return <path className="country" d={x} key={`${props.data.key}-${i}`}></path>
            })
        }
      <title>{props.data.value}</title>
    </g>
  );

}

interface Props {
  data: any,
  color: string | undefined,
  mapData: IMapReducer,
  onClick?: () => void,
  reduxActions: {
    setChosenCountry: (country: string) => void
  },
}


interface IMapStoreToProps {
  mapData:IMapReducer
}

interface IMapDispatchToProps {
  reduxActions: {
    setChosenCountry: (country: string) => void
  },
}

// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {
    mapData: { ...store.mapData }
  }
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {
      setChosenCountry: (country: string) => dispatch(setChosenCountry(country))
    }
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними
export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(Country);