import './Simulator.scss'
import {connect} from "react-redux";
import {NavLink} from "react-router-dom";
import React from "react";


function Simulator(props: Props) {
// ================================= STATE =================================

// ================================= FUNCTIONS =================================

// ================================= LAYOUT =================================
  return (
    <div className="simulator">
      <div className="button-wrapper">
      <NavLink to="filial_analytics" className="menu-button top left"><div style={{margin: "auto"}}>Анализ и разработка стратегии охвата рынка образования для различных уровней профобразования</div></NavLink>
      <NavLink to="filial_analytics2" className="menu-button top right"><div style={{margin: "auto"}}>Анализ и оптимизация экономических показателей филиальной сети</div></NavLink>
      </div>
      <div className="button-wrapper">
      <NavLink to="/main" className="menu-button bottom left"><div style={{margin: "auto"}}>Продвижение образовательной организации в рейтинге</div></NavLink>
      <NavLink to="/main" className="menu-button bottom right"><div style={{margin: "auto"}}>Отраслевой анализ и создание оптимального спектра предлагаемых специальностей/профессий</div></NavLink>
      </div>
    </div>
  );
}

// ================================= PROPS =================================

interface Props {

}

interface IMapStoreToProps {

}

interface IMapDispatchToProps {
  reduxActions: {},
}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {}
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {}
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(Simulator);