import './StartPage.scss'
import {connect} from "react-redux";
import startPageLogo from './../../sources/dias_logo.png'
import schema1 from './../../sources/svg/schema/schema1.svg'
import schema2 from './../../sources/svg/schema/schema2.svg'
import {NavLink} from "react-router-dom";

function StartPage(props: Props) {
// ================================= STATE =================================

// ================================= FUNCTIONS =================================

// ================================= LAYOUT =================================
  return (
    <div className="start-page">
      <div className="top-part">
        <img className="logo" src={startPageLogo} alt="logo"/>

        <div className="contacts-block">
          <div className="contact-wrapper">
            <div className="type">
              Телефон
            </div>
            <div className="data">
              +7 (499) 956-00-44
            </div>
          </div>
          <div className="contact-wrapper">
            <div className="type">
              E-mail
            </div>
            <div className="data">
              reserv@ranepa.ru
            </div>
          </div>
        </div>

      </div>

      <div className="bottom-part">

        <div className="headers">
          <div className="links">
            <div className="nav">Аналитика</div>
            <div className="nav">Моделирование</div>
            <div className="nav">Поддержка управленческих решений</div>
            <div className="nav">Аналитические панели</div>
          </div>
          <div className="enter">
            Войти
          </div>
        </div>

        <div className="title-and-button">
          <div className="title">
            МОДУЛЬ ЭКОНОМИЧЕСКОГО АНАЛИЗА РЕГИОНАЛЬНЫХ ОБРАЗОВАТЕЛЬНЫХ СИСТЕМ ДЛЯ МОДЕЛИРОВАНИЯ РАЗВИТИЯ ОБРАЗОВАТЕЛЬНЫХ ОРГАНИЗАЦИЙ
          </div>

          <NavLink to="main" className="button">
            Войти
          </NavLink>
        </div>

        <img className="schema1"  src={schema1} alt="1"/>
        <img className="schema2" src={schema2} alt="2"/>

      </div>

    </div>
  );
}

// ================================= PROPS =================================

interface Props {

}

interface IMapStoreToProps {

}

interface IMapDispatchToProps {
  reduxActions: {},
}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {}
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {}
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(StartPage);