import './ProfessionAnalysisWithMap.scss'
import {connect} from "react-redux";
import Map from "../../Map";
import _ from "lodash";
import {getRegionName} from "../../../mock/mockApi";
import {useEffect, useState} from "react";
import useDeepCompareEffect from "use-deep-compare-effect";
import {IAnalyzedCountries, IMapReducer} from "../../../redux/types";
import {replaceColors, setChosenCountry} from "../../../redux/actions/actions";
import {IMapsType} from "../../../types/filial";
import table from "../../../sources/svg/table.svg";
import map from "../../../sources/svg/map.svg";

function ProfessionAnalysisWithMap(props: Props) {
// ================================= STATE =================================

  const [slider, setSlider] = useState<any>({value: 0})
  const [sliderFlag, setSliderFlag] = useState<boolean>(false)
  const [chCountryData, setChCountryData] = useState<any>({initial: null, modified: null})
  const [isMap, setIsMap] = useState(true)




  useEffect(() => {
    if (props.analysis.length > 0 && props.chosenCountry && props.selectsData.sub_analysis.value === 'profession') {
      let index = props.analysis.findIndex((x: any) => x.region === props.chosenCountry)
      if (index > -1) setChCountryData({...chCountryData, initial: {..._.cloneDeep(props.analysis[index]), index: index}}); else setChCountryData({...chCountryData, initial: null})
    }
  }, [props.chosenCountry])

  useEffect(() => {
    setChCountryData({...chCountryData, modified: _.cloneDeep(chCountryData.initial)})
  }, [chCountryData.initial])



  useEffect(() => {
    if (sliderFlag) {
      if (props.analysis.length > 0) {
        let newAnalysis = _.cloneDeep(props.analysis)
        newAnalysis[chCountryData.modified.index].data[2021].ranh = Number(slider.value)
        props.setAnalysis(newAnalysis)
      }
      setSliderFlag(false)
    }
  }, [sliderFlag])

  useEffect(() => {
    if (chCountryData.modified) {
      if (chCountryData.modified.data[2021].ranh !== slider.value) {
        setSlider({value: chCountryData.modified.data[2021].ranh})
      }
    }
  }, [chCountryData.modified])


  useDeepCompareEffect(() => {

    if (props.analysis.length > 0 && props.selectsData.sub_analysis.value === 'profession') {
      getColors(props.analysis)
      if (props.chosenCountry) {
        let index = props.analysis.findIndex((x: any) => x.region === props.chosenCountry)
        if (index > -1) setChCountryData({...chCountryData, modified: {...props.analysis[index], index: index}}); else setChCountryData({...chCountryData, modified: null})
      }
    }
  }, [props.analysis])

// ================================= FUNCTIONS =================================


  const getColors = (result: any) => {
    let colors: IAnalyzedCountries = {
      darkgreen: [],
      lightgreen: [],
      yellow: [],
      red: []
    }

    result.forEach((x: any) => {
      let sum_dif = x.data[2021].summary - x.data[2020].summary
      let ranh_dif = x.data[2021].ranh - x.data[2020].ranh

      if (sum_dif > 0 && ranh_dif > 0) {
        colors.darkgreen.push(x.region)
      } else if (sum_dif > 0 && ranh_dif <= 0) {
        colors.red.push(x.region)
      } else if (sum_dif <= 0 && ranh_dif > 0) {
        colors.lightgreen.push(x.region)
      } else {
        colors.yellow.push(x.region)
      }

    })

    console.log(colors)

    props.reduxActions.replaceColors(colors)
    // props.setShowmap('profession')

  }

  const CalculateColor = (x: string) => {
    let newColored: any = {}
    _.transform(props.mapData.analyzedCountries, function(result, value, key) {
      value.forEach(x => {
        result[x] = key
      })
    }, newColored)
    return newColored[x] || ''
  }

// ================================= LAYOUT =================================
  return (
    <div className={`profession-analysis-with-map ${props.className || ''}`}>

      <Map className={isMap ? '' : '--invisible'}></Map>
      <table className={`data-table ${isMap ? '--invisible' : ''}`}>
        <thead>
        <tr>
          <th rowSpan={2}>Название региона</th>
          <th style={{textAlign: 'center'}} colSpan={2}>2020</th>
          <th style={{textAlign: 'center'}} colSpan={2}>2021</th>
        </tr>
        <tr>
          <th>Регион</th>
          <th>Филиал</th>
          <th>Регион</th>
          <th>Филиал</th>
        </tr>
        </thead>
        <tbody>
        {
          props.analysis.map((x: any) => {
            return(
              <tr className={`data-tr ${CalculateColor(x.region)}`} key={x.region} onClick={() => props.reduxActions.setChosenCountry(x.region)}>
                <td>{x.region_name}</td>
                <td>{x.data[2020].summary}</td>
                <td>{x.data[2020].ranh}</td>
                <td>{x.data[2021].summary}</td>
                <td>{x.data[2021].ranh}</td>
              </tr>
            )
          })
        }

        </tbody>
      </table>

      {
        isMap ? <img className="map-icon" src={table} alt="t" onClick={() => setIsMap(!isMap)}/> : <img className="map-icon" src={map} alt="m" onClick={() => setIsMap(!isMap)}/>
      }

            <div className="tags">
              {
                Object.values(props.selectsData).map((x: any) => {
                  return (
                    <div className='tag-wrapper' key={x.label+x.value}>
                      <span className="hashtag">#</span>
                      {x.label}
                    </div>
                  )
                })
              }
            </div>

      <div className={`region-data ${!props.chosenCountry ? '--invisible' : ''}`} id="region-data">

        <div className="block recommendations">
          {
            chCountryData.modified && chCountryData.initial ?
              <>

                <div className="slide-header">Изменение значения</div>
                <span>Поступившие по филиалу в 2021 году</span>
                <div className="slidecontainer">
                  <input
                    className="slider"
                    type="range"
                    max={Math.max(chCountryData.initial.data[2020].ranh, chCountryData.initial.data[2021].ranh) + 10}
                    min={Math.max(Math.min(chCountryData.initial.data[2020].ranh, chCountryData.initial.data[2021].ranh)-10, 0)}
                    step={1}
                    value={slider.value}
                    onMouseUp={(e: any) => {setSliderFlag(true)}}
                    onChange={(e: any) => {setSlider({value: Number(e.target.value)})}
                    }
                    // @ts-ignore
                    style={{'--minval': `"${Math.max(Math.min(chCountryData.initial.data[2020].ranh, chCountryData.initial.data[2021].ranh)-10, 0)}`, '--maxval':`"${Math.max(chCountryData.initial.data[2020].ranh, chCountryData.initial.data[2021].ranh) + 10}`}}
                  />
                </div>
              </>
              :
              null
          }



          <div className="header">Рекомендации</div>
          {
            chCountryData.modified ?
              <div className={`${(chCountryData.modified.data[2021].ranh - chCountryData.modified.data[2020].ranh) > 0 ? '--invisible' : ''}`}>{`Рекомендуемое управленческое решение - увеличить набор на ${Math.min(_.round((chCountryData.modified.data[2020].ranh - chCountryData.modified.data[2021].ranh + 1) / chCountryData.modified.data[2021].ranh *100, 3), 100)}%`}</div>
              :
              <div>Нет данных</div>
          }
        </div>

        <div className="block region">
          <div className="region-name">{`${getRegionName(props.chosenCountry)}`}</div>
          {
            chCountryData.modified ?
              <>
                <div>{`2020: Регион - ${chCountryData.modified.data[2020].summary}, Филиалы - ${chCountryData.modified.data[2020].ranh}`}</div>
                <div>{`2021: Регион - ${chCountryData.modified.data[2021].summary}, Филиалы - ${chCountryData.modified.data[2021].ranh}`}</div>
              </>
              :
              <div>Нет данных</div>
          }
        </div>

      </div>


    </div>
  );
}

// ================================= PROPS =================================

interface Props {
  analysis: any,
  mapData: IMapReducer,
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void,
    setChosenCountry: (country: string) => void

  },
  chosenCountry: string,
  setAnalysis: (data: any) => void,
  selectsData: any,
  setShowmap: (data: IMapsType) => void,
  className?: string,
  showmap: IMapsType
}

interface IMapStoreToProps {
  mapData: IMapReducer,

}

interface IMapDispatchToProps {
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void,
    setChosenCountry: (country: string) => void

  },

}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {
    mapData: { ...store.mapData }
  }
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {
      replaceColors: (coloredCountries: IAnalyzedCountries) => dispatch(replaceColors(coloredCountries)),
      setChosenCountry: (country: string) => dispatch(setChosenCountry(country))
    }
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(ProfessionAnalysisWithMap);