import {russia_map} from "../data/russia";
import Country from "./../components/Country";
import {IAnalyzedCountries, IMapReducer} from "../redux/types";
import {replaceColors, setChosenCountry} from "../redux/actions/actions";
import {connect} from "react-redux";
import {useEffect, useLayoutEffect, useState} from "react";
import _ from 'lodash'
import './Map.scss'

function Map(props: Props) {

  const [colored, setColored] = useState<any>({})
  const [zoom, setZoom] = useState<number>(100)

  useEffect(() => {
    let newColored: any = {}

    _.transform(props.mapData.analyzedCountries, function(result, value, key) {
      value.forEach(x => {
        result[x] = key
      })
    }, newColored)

    setColored(newColored)

  }, [props.mapData.analyzedCountries])

  useLayoutEffect(() => {
    return () => {
      props.reduxActions.setChosenCountry("")
    }
  }, [])

  return (
    <div className={`map-backhack ${props.className ? props.className : ""}`}>
      <div className="map-wrapper">
        <svg className="map" style={{width: String(zoom)+"%"}} viewBox="0 0 2362 1299" xmlns="http://www.w3.org/2000/svg">
          {
            russia_map.map(x => {
              return <Country data={x} key={x.key} color={colored[x.key]} onClick={() => props.reduxActions.setChosenCountry(x.key)}/>
            })
          }
        </svg>

      </div>
      <div className="zoom">
        <button className={`zoom_b plus ${zoom < 300 ? '' : '--inactive'}`} onClick={() => { if (zoom < 300) setZoom(zoom + 50) } }>+</button>
        <div className="separator"></div>
        <button className={`zoom_b minus ${zoom > 100 ? '' : '--inactive'}`} onClick={() => { if (zoom > 100) setZoom(zoom - 50) } }>-</button>
      </div>
    </div>
  );
}

interface Props {
  mapData: IMapReducer,
  className?: string,
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void
    setChosenCountry: (country: string) => void
  },
}


interface IMapStoreToProps {
  mapData:IMapReducer
}

interface IMapDispatchToProps {
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void
    setChosenCountry: (country: string) => void
  },
}

// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {
    mapData: { ...store.mapData }
  }
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {
      replaceColors: (coloredCountries: IAnalyzedCountries) => dispatch(replaceColors(coloredCountries)),
      setChosenCountry: (country: string) => dispatch(setChosenCountry(country))
    }
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними
export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(Map);
