import './WholePaidAnalysisWithMap.scss'
import {connect} from "react-redux";
import {useEffect, useState} from "react";
import _ from "lodash";
import useDeepCompareEffect from "use-deep-compare-effect";
import {IAnalyzedCountries, IMapReducer} from "../../../redux/types";
import Map from "../../Map";
import {getRegionName} from "../../../mock/mockApi";
import {replaceColors, setChosenCountry} from "../../../redux/actions/actions";
import {IMapsType} from "../../../types/filial";
import table from "../../../sources/svg/table.svg";
import map from "../../../sources/svg/map.svg";

function WholePaidAnalysisWithMap(props: Props) {
// ================================= STATE =================================
  const [slider, setSlider] = useState<any>({value: 0})
  const [sliderFlag, setSliderFlag] = useState<boolean>(false)
  const [chCountryData, setChCountryData] = useState<any>({initial: null, modified: null})
  const [isMap, setIsMap] = useState(true)



  useEffect(() => {
    if (props.analysis.length > 0 && props.chosenCountry && props.selectsData.sub_analysis.value === 'whole') {
      let index = props.analysis.findIndex((x: any) => x.region === props.chosenCountry)
      if (index > -1) setChCountryData({...chCountryData, initial: {..._.cloneDeep(props.analysis[index]), index: index}}); else setChCountryData({...chCountryData, initial: null})
    }
  }, [props.chosenCountry])

  useEffect(() => {
    setChCountryData({...chCountryData, modified: _.cloneDeep(chCountryData.initial)})
  }, [chCountryData.initial])



  useEffect(() => {
    if (sliderFlag) {
      if (props.analysis.length > 0) {
        let newAnalysis = _.cloneDeep(props.analysis)
        newAnalysis[chCountryData.modified.index].data.ranh.paid = Number(slider.value)
        props.setAnalysis(newAnalysis)
      }
      setSliderFlag(false)
    }
  }, [sliderFlag])

  useEffect(() => {
    if (chCountryData.modified) {
      if (chCountryData.modified.data.ranh.paid !== slider.value) {
        setSlider({value: chCountryData.modified.data.ranh.paid})
      }
    }
  }, [chCountryData.modified])


  useDeepCompareEffect(() => {
    if (props.analysis.length > 0 && props.selectsData.sub_analysis.value === 'whole') {
      getColors(props.analysis)
      if (props.chosenCountry) {
        let index = props.analysis.findIndex((x: any) => x.region === props.chosenCountry)
        if (index > -1) setChCountryData({...chCountryData, modified: {...props.analysis[index], index: index}}); else setChCountryData({...chCountryData, modified: null})
      }
    }
  }, [props.analysis])

// ================================= FUNCTIONS =================================


  const getColors = (result: any) => {
    let colors: IAnalyzedCountries = {
      darkgreen: [],
      lightgreen: [],
      yellow: [],
      red: []
    }

    result.forEach((x: any) => {
      let sum_ratio = x.data.summary.paid / x.data.summary.budget
      let ranh_ratio = x.data.ranh.paid / x.data.ranh.budget

      if (ranh_ratio / sum_ratio < 0.8 ) {
        colors.red.push(x.region)
      } else if (ranh_ratio / sum_ratio >= 0.8 && ranh_ratio / sum_ratio < 1.2) {
        colors.yellow.push(x.region)
      } else {
        colors.lightgreen.push(x.region)
      }

    })

    console.log(colors)

    props.reduxActions.replaceColors(colors)
    // props.setShowmap('wholepaid')

  }

  const CalculateMax = () => {
    if (chCountryData.initial) {
      return Math.ceil((chCountryData.initial.data.summary.paid * chCountryData.initial.data.ranh.budget * 1.2) / chCountryData.initial.data.summary.budget)
    }
    return 0
  }

  const CalculateMin = () => {
    if (chCountryData.initial) {
      return Math.ceil((chCountryData.initial.data.summary.paid * chCountryData.initial.data.ranh.budget * 0.8) / chCountryData.initial.data.summary.budget)
    }
    return 0

  }

  const CalculatePeople = (ratio: number) => {
    if (chCountryData.initial) {
      return Math.ceil((chCountryData.initial.data.summary.paid * chCountryData.initial.data.ranh.budget * ratio/100) / chCountryData.initial.data.summary.budget)
    }
    return 0
  }

  const Recalculate = () => {
    let input = document.getElementById('percent')
    if (input) {
      // @ts-ignore
      setSlider({value: CalculatePeople(input.value)})
      setSliderFlag(true)
    }
    return 0
  }

  const CalculateRatio= (x: any) => {
    let sum_ratio = x.data.summary.paid / x.data.summary.budget
    let ranh_ratio = x.data.ranh.paid / x.data.ranh.budget

    return _.round(ranh_ratio / sum_ratio*100, 2)
  }

  const CalculateColor = (x: string) => {
    let newColored: any = {}
    _.transform(props.mapData.analyzedCountries, function(result, value, key) {
      value.forEach(x => {
        result[x] = key
      })
    }, newColored)
    return newColored[x] || ''
  }

// ================================= LAYOUT =================================

  return (
    <div className={`whole-paid-analysis-with-map ${props.className || ''}`}>

      <Map className={isMap ? '' : '--invisible'}></Map>
      <table className={`data-table ${isMap ? '--invisible' : ''}`}>
       <thead>
       <tr>
         <th rowSpan={2}>Название региона</th>
         <th style={{textAlign: 'center'}} colSpan={3}>Регион</th>
         <th style={{textAlign: 'center'}} colSpan={3}>Филиал</th>
       </tr>
       <tr>
         <th>Платные</th>
         <th>Бесплатные</th>
         <th>Соотношение</th>
         <th>Платные</th>
         <th>Бесплатные</th>
         <th>Соотношение</th>
       </tr>
       </thead>
        <tbody>
        {
          props.analysis.map((x: any) => {
            return(
              <tr className={`data-tr ${CalculateColor(x.region)}`} key={x.region} onClick={() => props.reduxActions.setChosenCountry(x.region)}>
                <td>{x.region_name}</td>
                <td>{x.data.summary.paid}</td>
                <td>{x.data.summary.budget}</td>
                <td>{_.round(x.data.summary.paid / x.data.summary.budget * 100, 2)}</td>
                <td>{x.data.ranh.paid}</td>
                <td>{x.data.ranh.budget}</td>
                <td>{_.round(x.data.ranh.paid / x.data.ranh.budget * 100, 2)}</td>
              </tr>
            )
          })
        }

        </tbody>
      </table>

      {
        isMap ? <img className="map-icon" src={table} alt="t" onClick={() => setIsMap(!isMap)}/> : <img className="map-icon" src={map} alt="m" onClick={() => setIsMap(!isMap)}/>
      }
      <div className="tags">
        {
          Object.values(props.selectsData).map((x: any) => {
            return (
              <div className='tag-wrapper' key={x.label+x.value}>
                <span className="hashtag">#</span>
                {x.label}
              </div>
            )
          })
        }
      </div>

      <div className={`region-data ${!props.chosenCountry ? '--invisible' : ''}`} id="region-data">

        <div className="block recommendations">
          {
            chCountryData.modified && chCountryData.initial ?
              <>

                <div className="slide-header">Изменение значения</div>
                <span>Студенты на платном по филиалу</span>
                <div className="slidecontainer">
                  <input
                    className="slider"
                    type="range"
                    max={Math.max(CalculateMax(), chCountryData.initial.data.ranh.paid) + Math.round(CalculateMax()*0.1)}
                    min={Math.max(Math.min(CalculateMin(), chCountryData.initial.data.ranh.paid)-Math.round(CalculateMin()*0.1), 0)}
                    step={1}
                    value={slider.value}
                    onMouseUp={(e: any) => {setSliderFlag(true)}}
                    onChange={(e: any) => {setSlider({value: Number(e.target.value)})}
                    }
                    // @ts-ignore
                    style={{'--minval': `"${Math.max(Math.min(CalculateMin(), chCountryData.initial.data.ranh.paid)-Math.round(CalculateMin()*0.1), 0)}`, '--maxval':`"${Math.max(CalculateMax(), chCountryData.initial.data.ranh.paid) + Math.round(CalculateMax()*0.1)}`}}
                  />
                </div>
              </>
              :
              null
          }



          <div className="header">Рекомендации</div>
          {
            chCountryData.modified ?
              <div className={`${(chCountryData.modified.data.ranh.paid >= CalculateMax()) ? '--invisible' : ''}`}>{`Рекомендуемое управленческое решение - увеличить набор на ${_.round((CalculateMax() - chCountryData.modified.data.ranh.paid)  / chCountryData.modified.data.ranh.paid * 100, 2)}%`}</div>
              :
              <div>Нет данных</div>
          }
        </div>

        <div className="block region">
          <div className="region-name">{`${getRegionName(props.chosenCountry)}`}</div>
          {
            chCountryData.modified ?
              <>
                <div>{`Регион - платные: ${chCountryData.modified.data.summary.paid}, бюджет: ${chCountryData.modified.data.summary.budget}`}</div>
                <div>{`Филиалы - платные: ${chCountryData.modified.data.ranh.paid}, бюджет: ${chCountryData.modified.data.ranh.budget}`}</div>
                <div>{`Соотношение: ${_.round((chCountryData.modified.data.ranh.paid / chCountryData.modified.data.ranh.budget) / (chCountryData.modified.data.summary.paid / chCountryData.modified.data.summary.budget) * 100, 2)}`}</div>
                <br/>
                <div>
                  <label>Целевое соотношение (%)
                    <input id="percent" type="number" onChange={(e: any) => {console.log(e.target.value)}} />
                  </label>
                  <button className="recalculate" onClick={Recalculate}>Рассчитать</button>
                </div>

              </>
              :
              <div>Нет данных</div>
          }
        </div>

      </div>


    </div>
  );
}

// ================================= PROPS =================================

interface Props {
  analysis: any,
  mapData: IMapReducer,
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void,
    setChosenCountry: (country: string) => void
  },
  chosenCountry: string,
  setAnalysis: (data: any) => void,
  selectsData: any,
  setShowmap: (data: IMapsType) => void,
  className?: string,
  showmap: IMapsType
}

interface IMapStoreToProps {
  mapData: IMapReducer,
}

interface IMapDispatchToProps {
  reduxActions: {
    replaceColors: (coloredCountries: IAnalyzedCountries) => void,
    setChosenCountry: (country: string) => void
  },

}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {
    mapData: { ...store.mapData }
  }
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {
      replaceColors: (coloredCountries: IAnalyzedCountries) => dispatch(replaceColors(coloredCountries)),
      setChosenCountry: (country: string) => dispatch(setChosenCountry(country))
    }
  }
}
// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(WholePaidAnalysisWithMap);