import './SelectsContainer.scss'
import {connect} from "react-redux";
import Select from "react-select";
import {ISelectCondition, ISelectParams} from "../../../types/selects";

function SelectsContainer(props: Props) {
// ================================= STATE =================================


// ================================= FUNCTIONS =================================

  const findOptions = (condition: ISelectCondition) => {
    let selectholder = props.selects[condition.key]
    if (selectholder){
      let variant = condition.options.find((y) => { return y.value === selectholder.value })
      if (variant) {
        return variant.list
      } else {
        return []
      }
    } else return []

  }

// ================================= LAYOUT =================================
  return (
    <div className={`selects-container ${props.className || ''}`}>
      {
        props.data.map(x => {
          return (
            <Select
              options={x.condition ? findOptions(x.condition) : x.options}
              isDisabled={x.disabled}
              placeholder={x.placeholder}
              className={(x.className || "") + (x.condition ? findOptions(x.condition).length ? "": "--invisible" : "")}
              isClearable={false}
              isSearchable={true}
              name={x.id}
              onChange={(input) => props.setter(x.id, input)}
              key={x.id}
              components={{
                IndicatorSeparator: () => null
              }}
              theme={(theme) => ({
                ...theme,
                borderRadius: 8,
                colors: {
                  ...theme.colors,
                  primary25: '#D9D9D9',
                  primary50: '#c5c5c5',
                  primary: '#A30236',
                },
              })}
              styles={{
                control: (baseStyles) => ({
                  ...baseStyles,
                  borderColor: "#d9d9d9",
                  fontFamily: "ALS Hauss",
                  fontSize: 14
                }),
                placeholder: (defaultStyles) => {
                  return {
                    ...defaultStyles,
                    color: '#A0A0A0',
                  }
                },
                container: (base) => {
                  return {
                    ...base,
                    marginTop: 20
                  }
                }
              }}
            />
          )
        })
      }

    </div>
  );
}

// ================================= PROPS =================================

interface Props {
  data: ISelectParams[],
  setter: (key: string, value: any) => void,
  className?: string,
  selects: any
}

interface IMapStoreToProps {

}

interface IMapDispatchToProps {
  reduxActions: {},
}


// ================================= REDUX =================================

// Загружаем данные из store в props:
function mapStoreToProps(store: any): IMapStoreToProps {
  return {}
}

// Передаем в props объект reduxActions, который содержит в себе экшны для изменения store.
function mapDispatchToProps(dispatch: any): IMapDispatchToProps {
  return {
    reduxActions: {}
  }
}

// ================================= EXPORT =================================

// интерфейсы в коннекте нужны для того, чтобы в компонент не запихивать те поля, которые будут добавляться при mapStoreToProps и mapDispatchToProp
// собсна в эти интерфейсы надо запихать то, что будет в mapStoreToProps и mapDispatchToProp
// так же полезно запихать те же поля в основной интерфейс пропсов для удобства работы с ними

export default connect<IMapStoreToProps, IMapDispatchToProps>(mapStoreToProps, mapDispatchToProps)(SelectsContainer);