import actionTypes from "../actions/actionTypes";
import {IMapReducer} from "../types";

const initialState: IMapReducer = {
  chosenCountry: "",
  analyzedCountries: {
    darkgreen: [],
    lightgreen: [],
    yellow: [],
    red: []
  }
}

function mapReducer(state: IMapReducer = initialState, action: any) {
  switch (action.type) {
    case actionTypes.setChosenCountry:
      return {
        ...state,
        chosenCountry: action.country
      }
    case actionTypes.replaceColors:
      return {
        ...state,
        analyzedCountries: action.coloredCountries
      }
    default:
      return state
  }
}


export default mapReducer