export function setChosenCountry(country: string) {
  return {
    type: actionTypes.setChosenCountry, country
  }
}

export function replaceColors(coloredCountries: IAnalyzedCountries) {
  return {
    type: actionTypes.replaceColors, coloredCountries
  }
}